import axios from 'axios';


class ProcessAPI {

  host: string;
  port: number;
  baseAPI;

  constructor(host: string, port: number) {
    this.host = host;
    this.port = port;
    this.baseAPI = axios.create({
      baseURL: `http://${this.host}:${this.port}/`
    });
  }

  async getProcessList() {
    return this.baseAPI.get('procs')
  }

}

export default ProcessAPI;
